<template>
  <div class="container">
    <!-- 头部导航栏 -->
    <navbar>
      <h4 slot="title">我的关注</h4>
    </navbar>
    <!-- 关注列表组件 -->
    <attentionlist
      v-for="(item,index) in data"
      :key="index"
      @click.native="handleclick(item.id,index)"
    >
      <img :src="$axios.defaults.baseURL+item.head_img" slot="pic" />
      <h4 slot="title">{{item.nickname}}</h4>
      <p slot="date" class="date">{{moment(item.create_date).format("YYYY-MM-DD")}}</p>
    </attentionlist>
  </div>
</template>

<script>
import navbar from "@/components/navbar.vue";
import attentionlist from "@/components/attentionlist.vue";
// 引入第三方的日期格式处理的工具库
import moment from "moment";
export default {
  mounted() {
    const userdata = JSON.parse(localStorage.getItem("user_info"));
    this.userinfo = userdata;
    this.$axios({
      url: `/user_follows`,
      methods: "get",
      headers: {
        Authorization: userdata.token
      }
    }).then(res => {
      this.data = res.data.data;
    });
  },
  data() {
    return {
      data: [],
      moment,
      userinfo: {}
    };
  },
  methods: {
    handleclick(id, index) {
      // 提示用户是否要取消
      this.$dialog
        .confirm({
          title: "提示",
          message: "确定要取消关注吗？"
        })
        .then(res => {
          this.$axios({
            url: `/user_unfollow/${this.data[index].id}`,
            methods: "get",
            headers: {
              Authorization: this.userinfo.token
            }
          }).then(res => {
            console.log(res);

            this.data.splice(index, 1);
            this.$toast.success("取消关注成功");
          });
        })
        .catch(() => {
          // on cancel
        });
    }
  },
  components: {
    navbar,
    attentionlist
  }
};
</script>

<style lang="less" scoped>
.container {
  padding-top: 2rem;
}
</style>